<template>
	<view class="body">
		<view class="head">
			<view class="head_back"><image src="@/static/img/back.png" mode="" @click="navigateBack()"></image></view>
			<view class="head_center hei_38_bold">关注</view>
			<view class=" head_right"></view>
		</view>

		<view class="zi_body ">
			<view class="fensi_body">
				<view class="fensi_list" v-for="item in jilu_list">
					<view class="fensi_list_left">
						<view class="fensi_list_left_tx" @click="go_zhuye(item.userid)">
							<image :src="img_url + item.photourl" mode="" class="fensi_list_tx"></image>
							<image src="@/static/img/renzheng2.png" mode="" class="fensi_list_ren"></image>
						</view>
						<view class="fensi_list_left_r">
							<view class="hei_26 fensi_list_name" @click="go_zhuye(item.userid)">{{ item.nickname }}</view>
							<view class="hui_24">{{item.fensi}}个粉丝</view>
						</view>
					</view>
					<view class="fensi_list_right yiguan qian_26">已关注</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	onLoad() {
		this.huoqu_list();
	},
	onShow() {},

	data() {
		return {
			zhuangtai: 2,
			jilu_list: [],
			img_url: uni.getStorageSync('img_url')
		};
	},
	created() {},
	//下拉刷新
	// onPullDownRefresh: function() {
	// 	this.page=0
	// 	this.jilu_list=[]
	// 	this.is_all=false
	// 	this.huoqu_list()
	// },
	// stopPullDownRefresh:function(){

	// },
	methods: {
		//上拉加载
		onReachBottom() {
			// if(this.is_all){
			// 	uni.showToast({
			// 		title: '没有更多内容了',
			// 		duration: 2000,
			// 		icon: "none"
			// 	});
			// 	uni.stopPullDownRefresh();
			// 	return false
			// }else{
			// 	this.page++
			// 	this.huoqu_list()
			// }
		},
		navigateBack() {
			uni.navigateBack();
		},

		xq(id) {
			uni.navigateTo({
				url: 'tiwen_list_xq?id=' + id
			});
		},
		
		go_zhuye(id) {
			uni.navigateTo({
				url: 'ls_zhuye?lawyerid=' + id
			});
		},
		// 获取提问记录列表
		huoqu_list() {
			this.$http
				.post({
					url: '/mapi/index/guanzhulist'
				})
				.then(res => {
					this.jilu_list = res.data.list;
				});
		}
	},
	filters: {
		timeStamp: function(value) {
			if (value == null) {
				return 'null';
			}
			var i = (value + '').length;
			while (i++ < 13) value = value + '0';
			value = Number(value);
			var date = new Date(value);
			//date.setTime(value);
			var month = date.getMonth() + 1;
			var hours = date.getHours();
			if (hours < 10) hours = '0' + hours;
			var minutes = date.getMinutes();
			if (minutes < 10) minutes = '0' + minutes;
			var time = date.getFullYear() + '-' + month + '-' + date.getDate() + ' ' + hours + ':' + minutes;
			return time;
		}
	}
};
</script>

<style>
.head {
	border-bottom: 2rpx solid #e8e8e8;
