	position: relative;
	margin-right: 18rpx;
}
.fensi_list_ren {
	width: 15rpx;
	height: 16rpx;
	background-color: #ffffff;
	border: solid 2rpx #ffffff;
	position: absolute;
	right: 6rpx;
	bottom: 0;
	border-radius: 100%;
}
.fensi_list_left {
